"use client"

import type React from "react"
import { useState, useRef } from "react"
import { Sparkles, Code, Eye, Upload, Wand2, Save, RefreshCw } from "lucide-react"

export default function Home() {
  const [command, setCommand] = useState("")
  const [userId] = useState("default-user")
  const [generatedHtml, setGeneratedHtml] = useState("")
  const [templateId, setTemplateId] = useState("")
  const [changeQuery, setChangeQuery] = useState("")
  const [imageFile, setImageFile] = useState<File | null>(null)
  const [loading, setLoading] = useState(false)
  const [activeTab, setActiveTab] = useState<"preview" | "code">("preview")
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false)
  const fileInputRef = useRef<HTMLInputElement>(null)

  const handleGenerate = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setGeneratedHtml("")

    // Simulate API call
    setTimeout(() => {
      const mockHtml = `
        <div class="max-w-md mx-auto bg-white rounded-xl shadow-md overflow-hidden">
          <div class="p-6">
            <h2 class="text-2xl font-bold text-gray-900 mb-4">Contact Form</h2>
            <form class="space-y-4">
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-1">Name</label>
                <input type="text" class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500" placeholder="Your name">
              </div>
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-1">Email</label>
                <input type="email" class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500" placeholder="your@email.com">
              </div>
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-1">Message</label>
                <textarea class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500" rows="4" placeholder="Your message"></textarea>
              </div>
              <button type="submit" class="w-full bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 transition-colors">Send Message</button>
            </form>
          </div>
        </div>
      `
      setGeneratedHtml(mockHtml)
      setLoading(false)
    }, 2000)
  }

  const handleUpdateUI = async () => {
    if (!changeQuery.trim() || !generatedHtml) return
    setLoading(true)

    setTimeout(() => {
      setChangeQuery("")
      setLoading(false)
    }, 1500)
  }

  const saveUI = async (feedback: boolean) => {
    alert("UI saved successfully!")
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-pink-100 via-blue-100 to-purple-100 flex flex-col">
      {/* Header */}
      <header className="py-4 px-6 flex items-center justify-between border-b border-pink-200 bg-white/80 shadow-md">
        <div className="flex items-center space-x-2">
          <div className="w-8 h-8 bg-gradient-to-tr from-blue-500 to-pink-400 rounded-lg flex items-center justify-center shadow">
            <Sparkles className="w-5 h-5 text-white" />
          </div>
          <span className="text-xl font-semibold text-pink-700">AI UI Generator</span>
        </div>
        <nav className="hidden md:flex space-x-8">
          <a href="#" className="text-blue-500 hover:text-pink-500 transition-colors">Docs</a>
          <a href="#" className="text-blue-500 hover:text-pink-500 transition-colors">Examples</a>
          <a href="#" className="text-blue-500 hover:text-pink-500 transition-colors">API</a>
        </nav>
      </header>

      {/* Main Content */}
      <main className="flex-1 flex flex-col items-center justify-center px-2">
        {/* Results Section */}
        {(generatedHtml || loading) && (
          <div className="w-full max-w-2xl mt-8 mb-32 bg-white rounded-3xl shadow-2xl border-2 border-pink-200 overflow-hidden">
            {/* Tabs */}
            <div className="flex bg-gradient-to-r from-blue-100 to-pink-100 p-2 rounded-t-3xl">
              <button
                onClick={() => setActiveTab("preview")}
                className={`flex-1 py-3 text-lg font-medium rounded-2xl transition-all ${
                  activeTab === "preview"
                    ? "bg-gradient-to-r from-blue-400 to-pink-400 text-white shadow"
                    : "text-blue-600 hover:bg-blue-100"
                }`}
              >
                <Eye className="w-5 h-5 inline mr-2" />
                Preview
              </button>
              <button
                onClick={() => setActiveTab("code")}
                className={`flex-1 py-3 text-lg font-medium rounded-2xl transition-all ${
                  activeTab === "code"
                    ? "bg-gradient-to-r from-pink-400 to-blue-400 text-white shadow"
                    : "text-pink-600 hover:bg-pink-100"
                }`}
              >
                <Code className="w-5 h-5 inline mr-2" />
                Code
              </button>
            </div>
            {/* Content */}
            <div className="p-8 bg-gradient-to-br from-white via-pink-50 to-blue-50 min-h-[400px]">
              {loading ? (
                <div className="flex flex-col items-center justify-center py-24">
                  <RefreshCw className="w-8 h-8 text-pink-400 animate-spin mb-4" />
                  <h3 className="text-xl font-semibold text-pink-700 mb-2">Generating your UI...</h3>
                  <p className="text-blue-500">This usually takes a few seconds</p>
                </div>
              ) : (
                <div className="min-h-[400px]">
                  {activeTab === "preview" ? (
                    <div className="border-2 border-blue-200 rounded-2xl overflow-hidden bg-white shadow-lg">
                      <iframe
                        srcDoc={generatedHtml}
                        className="w-full h-[400px] bg-white"
                        title="UI Preview"
                        sandbox="allow-same-origin"
                      />
                    </div>
                  ) : (
                    <pre className="bg-blue-900/90 text-pink-200 p-6 rounded-2xl overflow-x-auto text-sm font-mono border-2 border-pink-200 shadow-inner">
                      {generatedHtml}
                    </pre>
                  )}
                </div>
              )}
            </div>
          </div>
        )}

        {/* Refine/Update UI */}
        {generatedHtml && !loading && (
          <div className="w-full max-w-2xl mb-36 bg-gradient-to-r from-pink-100 to-blue-100 rounded-2xl shadow-lg border-2 border-pink-200 p-8">
            <div className="flex items-center space-x-3 mb-6">
              <Wand2 className="w-6 h-6 text-pink-400" />
              <h3 className="text-xl font-semibold text-blue-700">Refine Your UI</h3>
            </div>
            <div className="flex flex-col sm:flex-row gap-4">
              <input
                type="text"
                value={changeQuery}
                onChange={(e) => setChangeQuery(e.target.value)}
                placeholder="Describe changes (e.g., 'Make the button blue and larger')"
                className="flex-1 px-6 py-4 border-2 border-blue-200 rounded-xl bg-white text-blue-700 placeholder-pink-400 focus:outline-none focus:ring-2 focus:ring-pink-400 focus:border-blue-400 transition-all"
              />
              <div className="flex gap-3">
                <button
                  onClick={handleUpdateUI}
                  disabled={!changeQuery.trim()}
                  className="px-8 py-4 bg-gradient-to-r from-blue-500 to-pink-500 text-white rounded-xl hover:from-blue-600 hover:to-pink-600 disabled:opacity-50 disabled:cursor-not-allowed transition-all font-medium shadow-lg"
                >
                  Update
                </button>
                <button
                  onClick={() => saveUI(true)}
                  className="flex items-center space-x-2 px-6 py-4 bg-gradient-to-r from-green-400 to-blue-400 text-white rounded-xl hover:from-green-500 hover:to-blue-500 transition-all font-medium shadow-lg"
                >
                  <Save className="w-4 h-4" />
                  <span>Save</span>
                </button>
              </div>
            </div>
          </div>
        )}
      </main>

      {/* Input Bar at Bottom */}
      <form
        onSubmit={handleGenerate}
        className="fixed bottom-0 left-0 w-full bg-gradient-to-r from-pink-100 to-blue-100 border-t-2 border-pink-200 px-4 py-6 z-50 shadow-2xl"
      >
        <div className="max-w-2xl mx-auto flex items-center gap-4">
          <button
            type="button"
            onClick={() => fileInputRef.current?.click()}
            className="flex items-center justify-center w-14 h-14 rounded-xl bg-gradient-to-tr from-blue-200 to-pink-200 hover:from-blue-300 hover:to-pink-300 transition-all border-2 border-blue-200"
            title="Upload reference image"
          >
            <Upload className="w-6 h-6 text-blue-500" />
          </button>
          <input
            ref={fileInputRef}
            type="file"
            accept="image/*"
            onChange={e => setImageFile(e.target.files?.[0] || null)}
            className="hidden"
          />
          <input
            type="text"
            value={command}
            onChange={(e) => setCommand(e.target.value)}
            placeholder="Describe your UI (e.g., 'Modern login form with logo')"
            className="flex-1 px-6 py-4 text-lg border-2 border-blue-200 rounded-xl bg-white text-blue-700 placeholder-pink-400 focus:outline-none focus:ring-2 focus:ring-pink-400 focus:border-blue-400 transition-all"
          />
          <button
            type="submit"
            disabled={loading || !command.trim()}
            className="flex items-center gap-3 px-8 py-4 bg-gradient-to-r from-blue-500 to-pink-500 text-white rounded-xl font-semibold text-lg hover:from-blue-600 hover:to-pink-600 disabled:opacity-50 disabled:cursor-not-allowed transition-all shadow-lg min-w-[140px] justify-center"
          >
            {loading ? (
              <>
                <RefreshCw className="w-5 h-5 animate-spin" />
                <span>Generating...</span>
              </>
            ) : (
              <>
                <Sparkles className="w-5 h-5" />
                <span>Generate</span>
              </>
            )}
          </button>
        </div>
      </form>
    </div>
  );
}
